import React from 'react';
import { Grid, GridItem } from '@chakra-ui/react'
import Social from './Social';

const Sidebar = ({ metadata, titleLinkHandler }) => {
  return (
    <Grid>
      <GridItem mb={2} sx={{
        '@media print': {
          display: 'none',
        },
      }}>
        <img
          className="img-fluid rounded"
          src={metadata.author.avatar}
          alt="Avatar"
          style={{ height: 200 }}
        />
      </GridItem>
      <GridItem>
        <h1>
          <a href="#" onClick={() => titleLinkHandler()}>
            {metadata.title}
          </a>
        </h1>
      </GridItem>
      <GridItem>
        <p>{metadata.description}</p>
      </GridItem>
      <GridItem>
        <Social social={metadata.author.social} />
      </GridItem>
    </Grid>
  );
};

export default Sidebar;
